div.container {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

h2 {
  text-align: center;
  flex: 0 0 100%;
  display: block;
  padding: 0;
}
div.table-responsive {
  display: flex;
  justify-content: center;
}
div.container a {
  margin: 2em 0;
  flex: 0 0 100%;
}
table tr:hover {
  background-color: blue;
  cursor: pointer;
  color: white;
}
div.container table {
  font-size: 0.9em;
}

.list-group {
  display: flex;
  flex-wrap: wrap;
  flex: 0 0 100%;
}

div.container a {
  flex: 0 0 100%;
  display: flex;
}
div.table-responsive table tr {
  display: block;
  margin-top: 2em;
  border: 1 px solid black;
}
div.table-responsive table tr td {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  font-weight: bold;
  border: 1 px solid black;
}
div.table-responsive table thead td {
  text-align: justify;
  font-size: 1.5em;
  font-weight: bold;
  text-transform: uppercase;
}
div.table-responsive table td button {
  height: 50px;
  margin: 1em 1em;
}
div.rides-card {
  margin: 1em;
  height: 100px;
    box-shadow: 1px 1px 1px 1px #463876, 1px 1px 1px 1px rgba(0, 0, 0, 0.14), 1px 2px 1px 2px rgba(0, 0, 0, 0.12);
    margin-top: 10px;
}
